@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  overflow: none;
}

.navbar {
    background-image: url(data:image/jpeg;base64,);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    z-index: 2;
}

.navbar-container {
    padding-top: 3em;
}

.navbar-logo {
    margin-left: 10em;
}

.navbar-bars {
    float: right;
    margin-right: 10em;
    transform: scale(1.5);
    color: #2A2A2A;
}

.nav-menu {
    background-color: #E1E1E1;
    width: 250px;
    float: right;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0em;
    right: -100%;
    transition: 850ms;
    border: 2px solid #2a2a2a;
    border-radius: 3px;
}

.menu-bars {
    list-style: none;
    color: #2A2A2A;
    margin-left: 10rem;
    background: none;
    transform: scale(1.5);
}

.nav-menu.active {
    right: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5em 1em 0.5em 1em;
    list-style: none;
    height: 4em;
}

.nav-text a {
    text-decoration: none;
    color: #2A2A2A;
    font-size: 18px;
    font-weight: 500;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 0 4em 0 4em;
    border-radius: 10px;
}

.nav-text a:hover {
    background-color: #BC8BD8;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #E1E1E1;
    width: 100%;
    height: 5em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.homeContainer {
    background-image: url(data:image/jpeg;base64,);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    height: 100%;
    z-index: 1;
  }
